
import { defineComponent } from 'vue'
import { Field, Option } from '@/components/Forms/FieldInterface'
import { ProgramService } from "@/services/program_service"
import { find, isEmpty } from "lodash"
import { nextTask } from "@/utils/WorkflowTaskHelper"
import { ENCOUNTER_GUIDELINES, FlowState } from "@/guidelines/encounter_guidelines"
import { matchToGuidelines } from "@/utils/GuidelineEngine"
import HisStandardForm from "@/components/Forms/HisStandardForm.vue";
import { toastDanger, toastWarning } from '@/utils/Alerts'
import Store from "@/composables/ApiStore"
import { Offline, isOfflineMode, isOnline } from '@/composables/offline'
import dayjs from 'dayjs'
import { AppEncounterService } from '@/services/app_encounter_service'
import { ObsValue } from '@/services/observation_service'

export default defineComponent({
    components: { HisStandardForm },
    data: () => ({
        offlineID: null as any,
        patient: {} as any,
        fields: [] as Array<Field>,
        patientID: '' as any,
        providerID: -1 as number,
        providers: [] as any,
        facts: {
            age: -1 as number,
            gender: '' as string,
            birthDate: '' as string,
            sessionDate: '' as string,
            apiDate: '' as string,
            isEnrolledInProgam: false as boolean,
            encounterName: 'N/A' as string,
            providers: [] as Array<any>,
            isBdeMode: false as boolean,
            outcome: '' as string,
            outcomeStartDate: '' as string,
            village: '' as string,
            district: '' as string,
            currentTA: '' as string,
        },
        formLabel: '' as string,
        ready: false
    }),
    created() {
        if (this.$route.params.patient_id) {
            this.offlineID = this.$route.params.patient_id
            this.patientID = parseInt(`${this.$route.params.patient_id}`);
            if (this.goOffline()) {
                this.initOfflineData()
            } else {
                Store.get('ACTIVE_PATIENT', { patientID: this.patientID})
                    .then(patientData => {
                        this.patient = patientData
                        if (!isEmpty(this.patient)) {
                            Offline().cachePatient(this.patient)
                        }
                        this.setEncounterFacts().then(() => {
                            this.checkEncounterGuidelines().then(() => {
                                this.ready = true
                            }).catch((e) => console.log(e))
                        }).catch(() => {
                            if (this.goOffline()) {
                                this.initOfflineData()
                            }
                        })
                    })
            }
        }
    },
    computed: {
        cancelDestination(): string { return this.patientDashboardUrl() }
    },
    methods: {
        goOffline(){
            return !isOnline.value || isOfflineMode.value
        },
        isOnline() {
            return isOnline.value
        },
        isOfflineMode() {
            return isOfflineMode.value
        },
        initOfflineData() {
            const offline = Offline().findByID(this.offlineID)
            const person = offline?.personParams?.person
            if (!person) return
            this.facts.birthDate = person.birthdate
            this.facts.age = dayjs(ProgramService.getSessionDate()).diff(
                this.facts.birthDate, 'years'
            )
            this.facts.district = person.current_district
            this.facts.village = person.current_village
            this.facts.currentTA = person.current_traditional_authority
            this.facts.gender = person.gender
            this.ready = true
        },
        async saveFormEncounterAndProceed(params={} as any) {
            let status = 'Pending'
            let created = false
            let obs = null
            try {
                obs = await this.resolveObs(params.computedData)
            } catch (e) {
                console.error(`${e}`)
                return toastWarning("Unable to resolve observations")
            }
            if (!this.isOfflineMode()) {
                try {
                    const enc = new AppEncounterService(
                        this.patientID,
                        params.encounterTypeID, 
                        this.providerID
                    )
                    if ((await enc.createEncounter())) {
                        if ((await enc.saveObservationList(obs as ObsValue[]))) {
                            status = 'Success'
                            created = true
                        }
                    }
                    if (typeof params.callback === 'function') {
                        await params.callback(enc)
                    }
                } catch (e) {
                    created = false
                    if (/NetworkError|Failed to fetch/i.test(`${e}`)) {
                        status = 'NetworkError'
                    } else {
                        status = 'Exception'
                        console.error(`Exception while saving an encounter: ${e}`)
                    }
                }
                if (status === 'Exception') {
                    return toastDanger("An error has occured!")
                }
            }
            Offline().saveObsOffline({
                observations: obs,
                id: this.offlineID,
                uploadStatus: status,
                encounterName: this.$route.name,
                encounterTypeID: params.encounterTypeID,
                meta: params.formData,
                labOrders: params.labOrders,
                created
            })
            if (!params.skipNextTask) this.nextTask()
        },
        runflowState(state: FlowState, params=null) {
            const states: any = {}
            states[FlowState.SET_PROVIDER] = (selection: any) => {
                const [ username ] = selection.split(' ')
                const provider = find(this.providers, { username })
                if (provider) this.providerID = provider.person_id
                return FlowState.CONTINUE
            }
            states[FlowState.CHANGE_SESSION_DATE] = () => { 
                this.$router.push(`/session/date?patient_dashboard_redirection_id=${this.patientID}`)
                return FlowState.EXIT
            }
            states[FlowState.CHANGE_PATIENT_OUTCOME] = () => {
                this.$router.push(`/patient/programs/${this.patientID}`)
                return FlowState.EXIT
            }
            states[FlowState.GO_TO_PATIENT_DASHBOARD] = () => {
                this.gotoPatientDashboard()
                return FlowState.EXIT
            }
            if (state in states) {
                return states[state](params)
            }
        },
        async checkEncounterGuidelines() {
            const findings = matchToGuidelines(this.facts, ENCOUNTER_GUIDELINES)
            for(const index in findings) {
                const finding = findings[index]
                if (finding?.actions?.alert) {
                    const status = this.runflowState((await finding?.actions?.alert(this.facts)))
                    if (status === FlowState.EXIT) return
                }
                if (finding?.actions?.selection) {
                    const selection = await finding?.actions?.selection(this.facts)
                    this.runflowState(selection.flowState, selection.value)
                }
            }
        },
        async setEncounterFacts() {
            try {
                const program = await Store.get('PATIENT_PROGRAM', { patientID: this.patientID })
                this.facts.isEnrolledInProgam = program.programID === ProgramService.getProgramID()
                this.facts.outcome = program.outcome
                this.facts.outcomeStartDate = program.startDate
            } catch (e) {
                console.error(e)
                toastDanger(`${e}`)
            }
            this.facts.currentTA = this.patient.getCurrentTA()
            this.facts.district = this.patient.getCurrentDistrict()
            this.facts.village = this.patient.getCurrentVillage()
            this.facts.sessionDate = ProgramService.getSessionDate()
            this.facts.apiDate = ProgramService.getCachedApiDate() as string
            this.facts.isBdeMode = ProgramService.isBDE() as boolean
            this.facts.birthDate = this.patient.getBirthdate()
            this.facts.age = this.patient.getAge()
            this.facts.encounterName = this.$route.name 
                ? this.$route.name.toString().toUpperCase()
                : 'N/A'
            if (ProgramService.isBDE()) {
                this.providers = await Store.get('PROVIDERS')
                this.facts.providers = this.providers
                    .sort((a: any, b: any) => {
                        const usernameA = a.username.toUpperCase()
                        const usernameB = b.username.toUpperCase()
                        return usernameA < usernameB ? -1 : usernameA > usernameB  ? 1 : 0
                    })
                    .map((p: any) => {
                        let name = `${p.username}`
                        if (!isEmpty(p?.person?.names)) {
                            const [ latestName ] = p.person?.names || []
                            name += ` (${latestName.given_name} ${latestName.family_name})`
                        }
                        return name
                    })
            }
        },
        toOption(label: string, other={}) {
            return {
                label,
                value: label,
                other
            }
        },
        mapStrToOptions(items: Array<string | Option>) {
            return items.map(i => typeof i === 'string' || typeof i === 'number'  
                ?  { label: i, value: i } : i 
            )
        },
        patientDashboardUrl(): string {
            return `/patient/dashboard/${this.$route.params.patient_id}`
        },
        gotoPatientDashboard() {
            return this.$router.push({path: this.patientDashboardUrl()})
        },
        async nextTask() {
            try {
                const id = this.offlineID || this.patientID
                await nextTask(id, this.$router, this.$route)
            } catch (e) {
                console.error("Workflow error " +  e)
                this.gotoPatientDashboard()
            }
        },
        yesNoOptions(): Option[] {
            return [
                { label: "Yes", value: "Yes" },
                { label: "No", value: "No" }
            ]
        },
        toYesNoOption(label: string, other: any={}): Option {
            return {
                label,
                value: '',
                other: {
                    ...other,
                    values: this.yesNoOptions()
                }
            } 
        },
        mapOptions(options: Array<string>) {
            return options.map((option) => {
                return { label: option, value: option }
            });        
        },
        toConditionalOptions(options: Array<Array<string | boolean | undefined>>) {
            const final: Option[] = []
            for(const option of options) {
                if (typeof option[1] === 'boolean') {
                    if (option[1]) final.push(this.toOption(option[0] as string))
                    continue
                }
                final.push(this.toOption(option[0] as string))
            }
            return final
        },
        yesNoUnknownOptions(): Option[] {
            return [
                { label: "Yes", value: "Yes" },
                { label: "No", value: "No" },
                { label: "Unknown", value: "Unknown" }
            ]
        },
        resolveObs(obs: any, tag='') {
            const values: any = Object.values(obs)
                .filter((d: any) => d && (d.tag === tag || tag === ''))
                .reduce((accum: any, cur: any) => { 
                    const data = cur.obs ? cur.obs : cur
                    if (Array.isArray(data)) {
                        accum = accum.concat(data)
                    } else if (typeof data === 'function') {
                        accum.push(data())
                    } else {
                        accum.push(data)
                    }
                    return accum
                    }, [])
            return Promise.all(values)
        },
        inArray(arr: Array<any>, expression: (i: any) => boolean): boolean {
            try {
                return arr.filter((i: any) => expression(i)).length > 0
            } catch (e) {
                return false
            }
        },
        validateSeries(conditions: Array<any>){
            try {
                for(const i in conditions) {
                    const condition = conditions[i]()

                    if (condition) return condition
                }
            } catch (e) {
                return [e]
            }
        }
    }
})
